import React from "react";
import { featured } from "./config/featured.json";
import FeaturedRow from "./components/FeaturedRow";
import { Container } from "react-bootstrap";

const Home = () => {
  const chunkArray = (arr) => {
    // Chunk array into subarrays of length 3 to help with grid creation
    return [].concat.apply(
      [],
      arr.map((elem, i) => {
        return i % 3 ? [] : [arr.slice(i, i + 3)];
      })
    );
  };

  const featuredGrid = chunkArray(featured);

  return (
    <div>
      <div style={{ textAlign: "center", marginBottom: "3rem" }}>
        <h2>Our Open Source Commitment</h2>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
          minim veniam, quis nostrud exercitation ullamco laboris nisi ut
          aliquip ex ea commodo consequat. Duis aute irure dolor in
          reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
          pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
          culpa qui officia deserunt mollit anim id est laborum.
        </p>
      </div>
      <div style={{ textAlign: "center" }}>
        <h2 style={{ marginBottom: "3rem" }}>Featured Open Source Projects</h2>
        <Container>
          {featuredGrid.map((row) => (
            <FeaturedRow key={row} rowItems={row} />
          ))}
        </Container>
      </div>
    </div>
  );
};

export default Home;
