import React from "react";
import { Row, Col } from "react-bootstrap";
import Repository from "./Repository";

const FeaturedRow = ({ rowItems }) => {
  return (
    <div>
      <Row>
        {rowItems.map((i) => (
          <Col key={i.name}>
            <Repository
              name={i.name}
              url={i.url}
              description={i.description}
              icon={i.logo}
            />
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default FeaturedRow;
